import React from 'react'
import './listing.css'
import {Link} from "react-router-dom";

function HeaderMain() {
    return (
        <>
        <header id="header">
            <div id="logo_main">
                <Link to="/"><p id="heading">Ed<sup>umato</sup></p></Link>
            </div>
            
            <div id="social">
                <Link to="/" id="login">Login</Link>
                <a href="#" id="createaccount">Create an account</a>
            </div>
        </header> 
        </>
    )
}

export default HeaderMain
