import React , {Component} from 'react';
import axios from 'axios';

 const url = "https://apiforeveryone.herokuapp.com/filter"

 const style = {
     color:'red'
 }

 const filter = {
    color:'red',
    boxShadow:"2px 2px 5px gray",
    padding:25,
    display:"inline-block",
    height:200,
    width:150
}

const input= {
    backgroundColor:"red"
}

 class CostFilter extends Component{

    filterCost = (event) => {
        let mealId= sessionStorage.getItem('mealId');
        let cost = (event.target.value).split('-');
        let lcost = cost[0];
        let hcost = cost[1]; 
        let filterUrl;
        if(event.target.value == ""){
            filterUrl= `${url}/${mealId}`
        }
        else{
            filterUrl= `${url}/${mealId}?lcost=${lcost}&hcost=${hcost}`
        }
        axios.get(filterUrl)
        .then((res) =>{this.props.restPerCost(res.data)})
    }

     render(){
         return(
        <>
            <div style={filter}>
            <center style={style}>cost filter</center>
            <div onChange={this.filterCost}> 
                <label className="radio">
                    <input type="radio" name="cuisine" value=""/>All
                </label>
                <label className="radio">
                    <input type="radio" name="cuisine" value="201-300"/>Rs 201-300
                </label>
                <label className="radio">
                    <input type="radio" name="cuisine" value="301-600" />Rs 301-600
                </label>
                <label className="radio">
                    <input type="radio" name="cuisine" value="601-1000" />Rs601-1000
                </label>
                <label className="radio">
                    <input type="radio" name="cuisine" value="1001-1500" />Rs 1001-1500
                </label>
            </div>
            </div>
        </>
         )
     }
 }


 export default CostFilter;